























import Vue from 'vue';
import {Component} from 'vue-property-decorator';

@Component
export default class NumberPad extends Vue {
 output = '0';
 num: string[] = [];
 count = 0;
  calculation(){
    let result;
    let n
    if (this.num.slice(0,3).indexOf('+') >= 0) {
      n = this.num.slice(0,3).join('').split('+').map(i => parseFloat(i))
      this.num = this.num.slice(3)
      result = (n[0] + n[1]).toString();
    }
    if (this.num.slice(1,3).indexOf('-') >= 0) {
      n = this.num.slice(0,3).join('').split('-').map(i => parseFloat(i))
      this.num =this.num.slice(3)
      result = (n[0] - n[1]).toString();
    }
    return result
  }
 inputContent(event: MouseEvent) {
  const button = (event.target as HTMLButtonElement);
  const input = button.textContent as string;
   const addIndex = this.output.indexOf('+')
   const subIndex = this.output.indexOf('-')
   if ((addIndex >= 0 || subIndex >= 0) && input !== '-' && input !== '+') {
    this.output = input;
    this.count = 1
    return;
   } else if ((addIndex >= 0 && input === '-' )||(subIndex >= 0 && input === '+' )){
    return;
   } else if ((addIndex >= 0 && input=== '+')||(subIndex>=0 && input=== '-' )){
    this.count = 1
    return;
   } else {
   if (addIndex >= 0 || subIndex >= 0) {
    this.output = input;
    this.count = 1
    return;
   }
   if (input === '+' || input === '-') {
    this.num.push(this.output);
   }
   if (addIndex === -1 && input === '+') {
    if (this.count <= 1){
     this.num.push('+')
    }
    this.count += 1
   }
   if (this.output.slice(1).indexOf('-') === -1 && input === '-') {
    if (this.count <= 1){
     this.num.push('-')
    }
    this.count += 1
   }
  }
  if (this.count >= 2) {
   const result = this.calculation()
    if (result){
      this.num.unshift(result);
      this.output = this.num[0];
      this.count = 1;
    }
  }
  if (this.output.length === 16) {
   return;
  }
  if (this.output === '0') {
   if ('0123456789'.indexOf(input) >= 0) {
    this.output = input;
   } else {
    this.output += input;
   }
   return;
  }
  if (this.output.indexOf('.') >= 0 && input === '.') {
   return;
  }
  this.output += input;

 }
 clear() {
  this.output = '0';
  this.num.length = 0;
  this.count = 0;
 }

 ok() {
   if (this.output.indexOf('+') >=0 || this.output.slice(1).indexOf('-') >= 0 || this.count === 0){
     const number = parseFloat(this.output);
     if (number >0){
       this.$emit('update:value', number);
       this.$emit('submit', number);
       this.output = '0';
     }else {
       alert('金额不能为0')
     }
   }else {
     this.num.push(this.output);
     const result = this.calculation()
     if (result){
       if (parseFloat(result) >0){
         this.$emit('update:value', result);
         this.$emit('submit', result);
         this.output = '0';
       }else {
         alert('金额不能为0')
       }
     }
   }
 }
}
