
















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Tabs from "@/components/Tabs.vue";
import recordTypeList from '@/constants/recordTypeList';
import groupedList from '@/lib/groupedList';
import dayjs from 'dayjs';
import Charts from '@/components/Charts.vue';
import _ from 'lodash'
@Component({
  components: {Charts, Tabs}
})
export default class Detail extends Vue {
  type='-';
  typeList = recordTypeList;
  get recordList(){
    return  this.$store.state.recordList
  }
  get gropedList(){
    return groupedList(this.recordList,this.type)
  }
  get tagList(){
    return this.$store.state.tagList
  }
  get keyValueList(){
    const startDay = dayjs().startOf('M')
    const lastDay = dayjs().endOf('M').format('D')
    const arr = []
    for (let i = 0; i < parseInt(lastDay);i++){
      const dateString = dayjs(startDay).add(i,'day').format('YYYY-MM-DD')
      const found = _.find(this.gropedList,{title:dateString})
      arr.push({
        key:dayjs(dateString).format('D'),value: found? found.total:0
      })
    }
    return arr
  }
  get barOptions(){
    const keys = this.keyValueList.map(item=>item.key)
    const values = this.keyValueList.map(item=>item.value)
    return{
      title: {
        show:true,
        text:'每日明细',
      },
      tooltip: {
        show: true,
      },
      legend: {
        data: ['金额']
      },
      grid:{
        width: 350,
        bottom:30,
        left: 'center',
      },
      xAxis: {
        type: 'category',
        data: keys,
        axisTick: {
          show: false
        },
        axisLine:{
          lineStyle: {
            width:1,
            height:100
          }
        },
        axisLabel:{
          margin:10,
        }
      },
      yAxis: {
        show: false,
        type: 'value',
        boundaryGap:true,
        splitLine:{
          show:true
        },
      },
      series: [{
        lineStyle: {},
        name: '金额',
        data: values,
        type: 'bar',
        barWidth: 6,
        itemStyle: {
          normal: {
            label: {
              formatter: function (params: any) {
                if (params.value > 0) {
                  return params.value
                } else {
                  return ''
                }
              },
              show: true,
              position: 'top',
              textStyle: {
                fontSize:12,
                color: '#333'
              }
            },
            color: '#eed385',
            barBorderRadius: [5, 5, 0, 0],
          },
        }
      }]
    }
  }
  get valueNameList(){
    const newList = this.newList().filter(r=>r.type === this.type)
    const tagList = this.tagList.map(((item: Tag) =>item.name))
    const arr: TagGroup[] = []
    for (let i = 0; i < tagList.length;i++){
      const found: any = _.filter(newList,{tags:tagList[i]})
      arr.push(
        {name:tagList[i],items:found}
      )
    }
    arr.map(
      group => group.value =group.items.reduce((sum,item)=>sum+item.amount,0)
    )
    const newArr = arr.map(r=> _.pick(r,['value','name']))
    newArr.map(item=> {
      if (item.value === 0) {
        item.value = ''
      }
      }
    )
    return newArr
  }
  get pieOptions(){
    const xx = this.valueNameList
    return{
      title: {
        text: '本月分析',
        top:10,
      },
      tooltip: {
        trigger: 'item',
        formatter: '{b} : {c} ({d}%)'
      },
      series: [
        {
          type: 'pie',
          left:'center',
          width:250,
          radius: '65%',
          center: ['50%', '50%'],
          selectedMode: 'single',
          data: xx,
          label: {
            show:true,
            formatter:'({d}%)\n{b}'
          },
          emphasis: {
            itemStyle: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)',
            }
          }
        }
      ]
    }
  }
  beautify(){
    return dayjs().format('YYYY年MM月')
  }
  newList(){
    const now = dayjs()
    const {recordList} = this;
    const result = []
    for (let i = 0; i < recordList.length;i++){
      if (dayjs(recordList[i].createdAt).isSame(now,'month')){
        result.push(recordList[i])
      }
    }
    return result
  }
  total(symbol: string){
    return this.newList().filter(i => i.type === symbol).reduce((sum,item)=> sum+item.amount,0)
  }
  created(){
    this.$store.commit('fetchRecords')
    this.$store.commit('fetchTags')
  }
}
